import React, { useState, useEffect, useMemo, useContext } from "react";
import M from "materialize-css";
import { Link, useParams } from "react-router-dom";
import { StudentContext } from "../Student";
import Config from '../../config/Config';
import { useTable, useSortBy, useGlobalFilter, usePagination } from "react-table";
import { format } from 'date-fns';

const center = {
    position: "relative",
    top: "20%",
}

function PlayVideo({ location }) {
    const { source, name, video_link } = location.state;
    const [isLoaded, setIsLoaded] = useState(true)
    return (
        <div className="page-wrapper p-0 m-0 bg-black">
            <div className="container">
                <div className="row p-0">
                    <div className="col-md-12 m-auto" style={{ height: "100vh", backgroundColor: "#000" }}>
                        <div className="" style={center}>
                            {source == "YOUTUBE" ? <span>
                                <iframe width="100%" height="315" src={`https://www.youtube.com/embed/${video_link || ""}?modestbranding=1&amp;rel=0`} title={name || ""} frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
                                <div style={{ position: "absolute", top: "0", background: "none", height: "100px", width: "100%" }} />
                            </span> : source == "DAILYMOTION" ? <iframe frameBorder="0" allowFullScreen width="100%" height="315" src={`//www.dailymotion.com/embed/video/${video_link}?ui-logo=0&queue-enable=false&sharing-enable=false&fullscreen=true`}></iframe> : ""}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default PlayVideo;
