import React, { useState, useEffect, useMemo, useContext } from "react";
import M from "materialize-css";
import { Link, useHistory, useParams } from "react-router-dom";
import { StudentContext } from "../Student";
import Config from '../../config/Config';
import { useTable, useSortBy, useGlobalFilter, usePagination } from "react-table";
import { format } from 'date-fns';

const GlobalFilter = ({ filter, setFilter }) => {
  return (
    <span className={""}>
      <input placeholder={"Query Here!"} className={"py-1 px-3"} value={filter || ""} onChange={(e) => setFilter(e.target.value)} />
    </span>
  )
}

function Videos() {
  // State Variable
  const history = useHistory();
  // const { state, dispatch } = useContext(StudentContext)
  const { type } = useParams();
  const [studentData, setStudentData] = useState(JSON.parse(localStorage.getItem("student")) || {});
  const [session, setSession] = useState({});
  const [allVideos, setAllVideos] = useState([]);
  const [isAllVideoLoaded, setIsAllVideoLoaded] = useState(false);
  const [data, setData] = useState({ name: "", video_link: "" });

  // Create Column for Table
  const COLUMNS = [
    {
      Header: "#SN",
      Cell: row => {
        return (Number(row.row.id) + 1)
      }
    },
    {
      Header: "PLAY",
      accessor: "",
      disableSortBy: true,
      Cell: row => {

        return (
          <div>
            <button
              type="button"
              className="ml-2 btn btn-danger footable-delete"
              // data-toggle="modal"
              // data-target="#viewModal"
              onClick={(evt) => history.push("/student/playVideo", { ...row.row.original })}
            >
              <span
                className="fas fa-play"
                aria-hidden="true"
              ></span>
            </button>
          </div>
        )
      }
    },
    {
      Header: "TITLE",
      accessor: "name",
    },
    {
      Header: "CHAPTER",
      accessor: "chapter.title",
    },
    {
      Header: "UPLOADED DATE",
      accessor: "created_date",
      Cell: ({ value }) => { return format(new Date(value), "dd/MM/yyyy") }
    },

  ];

  const columns = useMemo(() => COLUMNS, []);
  const rows_data = useMemo(() => allVideos, [allVideos]);
  const tableInstance = useTable(
    {
      columns,
      data: rows_data,
      initialState: {
        pageIndex: 0,
        pageSize: 10,
        sortBy: [
          {
            id: "asset",
            desc: false,
          },
        ],
      },
    },
    useGlobalFilter, useSortBy, usePagination
  );

  // destructuring the table instance
  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    page,
    prepareRow,
    state,
    nextPage,
    previousPage,
    canNextPage,
    canPreviousPage,
    pageOptions,
    gotoPage,
    pageCount,
    setPageSize,
    setGlobalFilter
  } = tableInstance;

  // Destructuring the state
  const { globalFilter } = state;
  // Destructuring the state
  const { pageIndex, pageSize } = state;
  // Fetching the data
  useEffect(() => {
    // Find selected Session
    const availableSession = studentData.session;
    const found = availableSession.find(({ session }) => session._id == JSON.parse(localStorage.getItem("studentSelectedSession"))._id);
    setSession(found);
    const sessionId = found.session._id;
    const batchId = found.batch._id;

    fetch(Config.SERVER_URL + "/student/searchVideos?session=" + sessionId + "&batch=" + batchId + "&type=" + type, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.getItem("jwt_student_token")}`,
      },
    })
      .then((res) => res.json())
      .then(
        (result) => {
          // setIsallChaptersLoaded(false);
          if (result.success) {
            setAllVideos(result.data || []);
            setIsAllVideoLoaded(true);
          } else {
            M.toast({ html: result.message, classes: "bg-danger" });
          }
        },
        (error) => {
          M.toast({ html: error, classes: "bg-danger" });
          setIsAllVideoLoaded(true);
        }
      );
  }, []);

  return (
    <div className="page-wrapper pt-0" style={{ height: "100%" }}>
      <div className="container-fluid">
        {/* Bread crumb and right sidebar toggle */}
        <div className="row page-titles mb-0">
          <div className="col-md-5 col-8 align-self-center">
            <h3 className="text-themecolor m-b-0 m-t-0">Videos</h3>
            <ol className="breadcrumb">
              <li className="breadcrumb-item">
                <Link to="/student">Home</Link>
              </li>
              <li className="breadcrumb-item active">videos</li>
            </ol>
          </div>
        </div>
        {/* End Bread crumb and right sidebar toggle */}

        {/* Start Page Content */}
        {/* Row */}
        <div className={"row page-titles mb-0 px-1 shadow-none"} style={{ background: "none" }}>
          {/* Video */}
          <div className="col-lg-12 col-xlg-12 col-md-12 px-0">
            <div className={"card mb-0 mt-2 border-0 rounded-0"}>
              <div className={"card-body pb-0 pt-2"}>
                <div>
                  <h4 className="float-left mt-2 mr-2">Search: {" "} </h4>
                  <GlobalFilter filter={globalFilter} setFilter={setGlobalFilter} />

                </div>
              </div>
            </div>


            {/* Data */}
            {isAllVideoLoaded ? (
              <div className="card border-0 rounded-0 m-0 py-1">
                {allVideos.length ? (
                  <div className="card-body py-0 px-1">
                    <div className="table-responsive">

                      <table
                        {...getTableProps()}
                        className={"table table-bordered table-striped my-0"}
                      >
                        <thead>
                          {
                            // Loop over the header rows
                            headerGroups.map((headerGroup) => (
                              // Apply the header row props
                              <tr {...headerGroup.getHeaderGroupProps()}>
                                {
                                  // Loop over the headers in each row
                                  headerGroup.headers.map((column) => (
                                    // Apply the header cell props
                                    <th {...column.getHeaderProps(column.getSortByToggleProps())}>
                                      {
                                        // Render the header
                                        column.render("Header")
                                      }
                                      <span>
                                        {column.isSorted ? (column.isSortedDesc ? ' 🔽' : ' 🔼') : ''}
                                      </span>
                                    </th>
                                  ))
                                }
                              </tr>
                            ))
                          }
                        </thead>
                        <tbody {...getTableBodyProps()}>
                          {page.map((row) => {
                            prepareRow(row);
                            return (
                              <tr {...row.getRowProps()}>
                                {row.cells.map((cell) => {
                                  return (
                                    <td {...cell.getCellProps()}>
                                      {" "}
                                      {cell.render("Cell")}{" "}
                                    </td>
                                  );
                                })}
                              </tr>
                            );
                          })}
                        </tbody>
                      </table>
                      <div>
                        <span>
                          Page:{' '}
                          <strong>
                            {pageIndex + 1} of {pageOptions.length}
                          </strong>
                        </span>
                        <select value={pageSize} onChange={(e) => setPageSize(e.target.value)}>
                          {
                            [10, pageCount * 10].map((val, i) => {
                              return (
                                <option key={i} value={val}> {val} </option>
                              )
                            })
                          }
                        </select>
                        <button onClick={() => gotoPage(0)} disabled={!canPreviousPage}> {'<<'} </button>
                        <button onClick={() => previousPage()} disabled={!canPreviousPage} >Previous</button>
                        <button onClick={() => nextPage()} disabled={!canNextPage} >Next</button>
                        <button onClick={() => gotoPage(pageCount - 1)} disabled={!canNextPage}> {'>>'} </button>
                      </div>

                    </div>
                  </div>
                ) : (
                  <div
                    className={
                      "alert alert-danger mx-3 rounded-0 border-0 py-2"
                    }
                  >
                    No Data Available
                  </div>
                )}
              </div>
            ) : (
              <div className={"bg-white p-3 text-center"}>
                <span
                  className="spinner-border spinner-border-sm mr-1"
                  role="status"
                  aria-hidden="true"
                ></span>
                Loading..
              </div>
            )}
          </div>
          {/* End Video */}
        </div>
        {/* Row */}
        {/* End PAge Content */}
      </div>

      {/* Modal Designing */}
      <div>
        {/* -- View Modal -- */}
        <div
          className="modal fade"
          id="viewModal"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="viewModalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog model-lg" role="document">
            <div className="modal-content rounded-0">
              <div className="modal-header">
                <h5 className="modal-title">Play Video</h5>
                <button type="button" className="close" data-dismiss="modal" aria-label="Close" onClick={() => setData({})}>
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div className="modal-body" style={{ position: "relative" }}>
                {data.source == "YOUTUBE" ? <span>
                  <iframe width="100%" height="315" src={`https://www.youtube.com/embed/${data.video_link || ""}?modestbranding=1&amp;rel=0`} title={data.name || ""} frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
                  <div style={{ position: "absolute", top: "0", background: "none", height: "100px", width: "100%" }} />
                </span> : data.source == "DAILYMOTION" ? <iframe frameBorder="0" allowFullScreen width="100%" height="315" src={`//www.dailymotion.com/embed/video/${data.video_link}?ui-logo=0&queue-enable=false&sharing-enable=false&fullscreen=true`}></iframe> : ""}
              </div>
            </div>
          </div>
        </div>

      </div>
    </div>
  );
}

export default Videos;
