import React, { useState, useEffect } from "react";
import M from "materialize-css";
import $ from "jquery";
import { Link } from "react-router-dom";
import Config from "../../config/Config";

//  Component Function
const StaffRights = (props) => {
  const [addingFormData, setAddingFormData] = useState({
    // Personal Details
    name: "",
    gender: "",
    dob: "",
    mobile: "",
    email: "",
    password:"",
    address: "",
    state: "",
    city: "",
    profile_picture: "",
  });

  const [selectedSession, setSelectedSession] = useState(
    localStorage.getItem("branchSession") || ""
  );
  const [isAddLaoded, setIsAddLaoded] = useState(true);
  const [isUpdateLaoded, setIsUpdateLaoded] = useState(true);
  const [isDeleteLaoded, setIsDeleteLaoded] = useState(true);
  const [isAdded, setIsAdded] = useState(false);
  const [isAllStaffLoaded, setIsAllStaffLoaded] = useState(true);
  const [allStaff, setAllStaff] = useState([]);

  const [data, setData] = useState({
      standard:{_id:""}
  }
  );

  const [viewData, setViewData] = useState({
    standard: {name:""},
    course_type: {name:""}
  });

  const [isUpdated, setIsUpdated] = useState(false);
  const [isDeleted, setIsDeleted] = useState(false);
  const [deleteId, setDeleteId] = useState("");

  // Update the state while clicking the View button
  const updateStateForViewData = (list) => {
    setViewData(JSON.parse(JSON.stringify(list)));
  };

  // Update the state while clicking the edit button
  const updateState = (list) => {
    setData(JSON.parse(JSON.stringify(list)));
  };


  // Update Submit Handler
  const updateSubmitHandler = (evt) => {
    
    setIsUpdated(false);
    setIsUpdateLaoded(false);
    evt.preventDefault(); 
     
    fetch(Config.SERVER_URL + "/branch/updateStaff", {
      method: "PUT",
      body: JSON.stringify(data),
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.getItem("jwt_branch_token")}`,
      },
    })
      .then((res) => res.json())
      .then(
        (result) => {
          setIsUpdateLaoded(true);
          if (result.success) {
            M.toast({ html: result.message, classes: "bg-success" });
            setIsUpdated(true);
            setData({})
            $("#closeUpdateModalButton").click();
            $("#closeUpdateStatusModalButton").click();
          } else {
            if (result.name)
              M.toast({ html: result.name, classes: "bg-danger" });
            if (result.gender)
              M.toast({ html: result.gender, classes: "bg-danger" });
            if (result.mobile)
              M.toast({ html: result.mobile, classes: "bg-danger" });
            if (result.address)
              M.toast({ html: result.address, classes: "bg-danger" });
            if (result.state)
              M.toast({ html: result.state, classes: "bg-danger" });
            if (result.city)
              M.toast({ html: result.city, classes: "bg-danger" });

            if (result.message)
              M.toast({ html: result.message, classes: "bg-danger" });
          }
        },
        (error) => {
          setIsUpdateLaoded(true);
          M.toast({ html: error, classes: "bg-danger" });
        }
      );
  };

  // Delete Submit Handler
  const deleteSubmitHandler = () => {
    setIsDeleted(false);
    setIsDeleteLaoded(false);
    const staffId = {
      _id: deleteId,
    };

    fetch(Config.SERVER_URL + "/branch/deleteStaff", {
      method: "DELETE",
      body: JSON.stringify(staffId),
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.getItem("jwt_branch_token")}`,
      },
    })
      .then((res) => res.json())
      .then(
        (result) => {
          setIsDeleteLaoded(true);
          if (result.success) {
            M.toast({ html: result.message, classes: "bg-success" });
            setIsDeleted(true);
            $("#closeDeleteModalButton").click();
            setDeleteId("");
          } else {
            if (result.Id) M.toast({ html: result._id, classes: "bg-danger" });
          }
        },
        (error) => {
          setIsDeleteLaoded(true);
          M.toast({ html: error, classes: "bg-danger" });
        }
      );
  };

  // Submit Handler
  const submitHandler = (evt) => {
    setIsAdded(false);
    setIsAddLaoded(false);

    evt.preventDefault();
    fetch(Config.SERVER_URL + "/branch/addStaff", {
      method: "POST",
      body: JSON.stringify(addingFormData),
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.getItem("jwt_branch_token")}`,
      },
    })
      .then((res) => res.json())
      .then(
        (result) => {
          setIsAddLaoded(true);
          if (result.success) {
            M.toast({ html: result.message, classes: "bg-success" });
            $("#closeAddModalButton").click();
            setIsAdded(true);
            setAddingFormData({
              name: "",
              comment: "",
            });
          } else {
            if (result.name)
              M.toast({ html: result.name, classes: "bg-danger" });
            if (result.gender)
              M.toast({ html: result.gender, classes: "bg-danger" });
            if (result.password)
              M.toast({ html: result.password, classes: "bg-danger" });
            if (result.mobile)
              M.toast({ html: result.mobile, classes: "bg-danger" });
            if (result.address)
              M.toast({ html: result.address, classes: "bg-danger" });
            if (result.state)
              M.toast({ html: result.state, classes: "bg-danger" });
            if (result.city)
              M.toast({ html: result.city, classes: "bg-danger" });
            if (result.message)
              M.toast({ html: result.message, classes: "bg-danger" });
          }
        },
        (error) => {
          setIsAddLaoded(true);
          M.toast({ html: error, classes: "bg-danger" });
        }
      );
  };

  // Get All Batch
  useEffect(() => {
    setIsAllStaffLoaded(true);
    // Get All Staff Data
    fetch(
      Config.SERVER_URL + "/branch/searchStaff?status=1",
      {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${localStorage.getItem("jwt_branch_token")}`,
        },
      }
    )
      .then((res) => res.json())
      .then(
        (result) => {
          if (result.success) {
            setAllStaff(result.data || []);
            setIsAllStaffLoaded(false);
          } else {
            M.toast({ html: result.message, classes: "bg-danger" });
            if (result.session)
              M.toast({ html: result.session, classes: "bg-danger" });
          }
        },
        (error) => {
          M.toast({ html: error, classes: "bg-danger" });
          setIsAllStaffLoaded(false);
        }
      );
  }, [isAdded, isUpdated, isDeleted]);

  // Return function
  return (
    <div className="page-wrapper">
      <div className={"container-fluid"}>
        {/* Bread crumb and right sidebar toggle */}
        <div className="row page-titles">
          <div className="col-md-5 col-8 align-self-center">
            <h3 className="text-themecolor m-b-0 m-t-0">
              Manage Staff Rights
            </h3>
            <ol className="breadcrumb">
              <li className="breadcrumb-item">
                <Link to="/branch">Branch</Link>
              </li>
              <li className="breadcrumb-item active">Staff Rights</li>
            </ol>
          </div>
        </div>
        {/* End Bread crumb and right sidebar toggle */}

        <div className={"row p-0"}>
          <div className={"col-md-4 p-0"}>
            {/* Heading */}
            <div className={"card mb-0 mt-2 border-0 rounded-0"}>
              <div className={"card-body pb-0 pt-2"}>
                  
                  <form className={"form-material"}>
                    <div className={"form-group"}>
                        <select className={"form-control shadow-sm"}>
                            <option>Select Staff</option>
                            {allStaff.map((value, index)=>(
                                <option value={value._id} key={index}> {value.name} </option>
                            ))}
                        </select>
                    </div>
                  </form>
                
              </div>
            </div>
          </div>
        </div>

        {/* Rights */}
        <div className={"row p-0"}>
          <div className={"col-md-12 p-0"}>
            <form className={"form-material"}>
              <div className={"row"}>

                  {/* Setting Section */}
                  <div className={"col-md-3"}>
                    <div className={"card mb-0 mt-2 border-0 rounded-0"}>
                        <div class="card-header bg-theme text-white">
                            Setting
                        </div>
                        <div className={"card-body pb-0 pt-2 px-0"} style={{height:"285px", overflowY:"scroll"}}>
                            <div className="px-2">
                                <input type="checkbox" className="" id="changePwd"/>
                                <label className="" for="changePwd">Change Password</label>
                            </div>
                            <div className="px-2">
                                <input type="checkbox" className="" id="prfSetting"/>
                                <label className="" for="prfSetting">Profile Setting</label>
                            </div>
                        </div>
                    </div>
                  </div>
                
                  {/* Setup Section */}
                  <div className={"col-md-3"}>
                    <div className={"card mb-0 mt-2 border-0 rounded-0"}>
                        <div class="card-header bg-theme text-white">
                            Setup
                        </div>
                        <div className={"card-body pb-0 pt-2 px-0"} style={{height:"285px", overflowY:"scroll"}}>
                            <div className="px-2">
                                <input type="checkbox" className="" id="addSession"/>
                                <label className="" for="addSession">Add Year/Session</label>
                            </div>
                            <div className="px-2">
                                <input type="checkbox" className="" id="manageSession"/>
                                <label className="" for="manageSession">Manage Year/Session</label>
                            </div>
                            <div className="px-2">
                                <input type="checkbox" className="" id="addStandard"/>
                                <label className="" for="addStandard">Add Standard</label>
                            </div>
                            <div className="px-2">
                                <input type="checkbox" className="" id="manageStandard"/>
                                <label className="" for="manageStandard">Manage Standard</label>
                            </div>
                            <div className="px-2">
                                <input type="checkbox" className="" id="addCourseType"/>
                                <label className="" for="addCourseType">Add CourseType</label>
                            </div>
                            <div className="px-2">
                                <input type="checkbox" className="" id="manageCourseType"/>
                                <label className="" for="manageCourseType">Manage CourseType</label>
                            </div>
                            <div className="px-2">
                                <input type="checkbox" className="" id="addClass"/>
                                <label className="" for="addClass">Add Class/Batch</label>
                            </div>
                            <div className="px-2">
                                <input type="checkbox" className="" id="manageClass"/>
                                <label className="" for="manageClass">Manage Class/Batch</label>
                            </div>
                            <div className="px-2">
                                <input type="checkbox" className="" id="addCourseType"/>
                                <label className="" for="addCourseType">Add CourseType</label>
                            </div>
                            <div className="px-2">
                                <input type="checkbox" className="" id="manageCourseType"/>
                                <label className="" for="manageCourseType">Manage CourseType</label>
                            </div>
                            <div className="px-2">
                                <input type="checkbox" className="" id="addClass"/>
                                <label className="" for="addClass">Add Class/Batch</label>
                            </div>
                            <div className="px-2">
                                <input type="checkbox" className="" id="manageClass"/>
                                <label className="" for="manageClass">Manage Class/Batch</label>
                            </div>
                            
                        </div>
                    </div>
                  </div>
              
                  {/* Staff Section */}
                  <div className={"col-md-3"}>
                    <div className={"card mb-0 mt-2 border-0 rounded-0"}>
                        <div class="card-header bg-theme text-white">
                            Staff
                        </div>
                        <div className={"card-body pb-0 pt-2 px-0"} style={{height:"285px", overflowY:"scroll"}}>
                            <div className="px-2">
                                <input type="checkbox" className="" id="addStaff"/>
                                <label className="" for="addStaff">Add Staff</label>
                            </div>
                            <div className="px-2">
                                <input type="checkbox" className="" id="manageStaff"/>
                                <label className="" for="manageStaff">Manage Staff</label>
                            </div>
                            <div className="px-2">
                                <input type="checkbox" className="" id="addStaffRights"/>
                                <label className="" for="addStaffRights">Manage Staff Rights</label>
                            </div>
                            <div className="px-2">
                                <input type="checkbox" className="" id="manageStandard"/>
                                <label className="" for="manageStandard">Manage Staff Action Rights</label>
                            </div>
                            <div className="px-2">
                                <input type="checkbox" className="" id="staffAttendence"/>
                                <label className="" for="staffAttendence">Staff Attendence</label>
                            </div>
                        </div>
                    </div>
                  </div>

                  {/* Student */}
                  <div className={"col-md-3"}>
                    <div className={"card mb-0 mt-2 border-0 rounded-0"}>
                        <div class="card-header bg-theme text-white">
                            Students
                        </div>
                        <div className={"card-body pb-0 pt-2 px-0"} style={{height:"285px", overflowY:"scroll"}}>
                            <div className="px-2">
                                <input type="checkbox" className="" id="studentInquiry"/>
                                <label className="" for="studentInquiry">Student Inquiry</label>
                            </div>
                            <div className="px-2">
                                <input type="checkbox" className="" id="studebtRegistration"/>
                                <label className="" for="studebtRegistration">Student Registration</label>
                            </div>
                            
                        </div>
                    </div>
                  </div>

              </div>
            </form>
          </div>
        </div>

        {/* -- Modal Designing -- */}
        <div>
          {/* -- Add Modal -- */}
          <div className="modal fade" id="addModal">
            <div className="modal-dialog modal-xl" role="document">
              <div className="modal-content rounded-0">
                <div className="modal-body">
                  <div className={"text-center"}>
                    <button type="button" className="close p-2 border" data-dismiss="modal" aria-label="Close">
                      <span aria-hidden="true">&times;</span>
                    </button>
                    <h4 className={"text-theme"}>Sraff Registration</h4>
                    <div className={"devider m-auto"} />
                  </div>

                  <form
                    onSubmit={submitHandler}
                    className="form-horizontal form-material"
                  >
                    {/* Personal Details */}
                    <div className={"row"}>
                      <div className={"col-md-12"}>
                        <h4 className={"text-theme"}>Personal Details</h4>
                        <div className={"devider"} />
                      </div>

                      {/* Staff Full Name */}
                      <div className={"col-md-4"}>
                        <div className={"form-group mb-3"}>
                          <label className={"text-dark h6"}>
                            Staff Full Name
                          </label>
                          <input
                            type="text"
                            onChange={(evt) =>
                              setAddingFormData({
                                ...addingFormData,
                                name: evt.target.value,
                              })
                            }
                            value={addingFormData.name}
                            className="form-control"
                            placeholder={"Eg. Rahul Kumar"}
                          />
                        </div>
                      </div>

                      {/* Gender */}
                      <div className={"col-md-4"}>
                        <div className={"form-group"}>
                          <label className={"text-dark h6"}>Gender</label>
                          <select
                            className="form-control"
                            onChange={(evt) =>
                              setAddingFormData({
                                ...addingFormData,
                                gender: evt.target.value,
                              })
                            }
                          >
                            <option value={""} disabled selected hidden>
                              Select Gender
                            </option>
                            <option value={"MALE"}>MALE</option>
                            <option value={"FEMALE"}>FEMALE</option>
                            <option value={"OTHER"}>OTHER</option>
                          </select>
                        </div>
                      </div>
 
                      {/* Staff Mobile */}
                      <div className={"col-md-4"}>
                        <div className={"form-group mb-3"}>
                          <label className={"text-dark h6"}>
                            Mobile
                          </label>
                          <input
                            type="tel"
                            onChange={(evt) =>
                              setAddingFormData({
                                ...addingFormData,
                                mobile: evt.target.value,
                              })
                            }
                            value={addingFormData.mobile}
                            className="form-control"
                            placeholder={"Mobile NUmber"}
                          />
                        </div>
                      </div>

                      {/* Staff Email */}
                      <div className={"col-md-4"}>
                        <div className={"form-group mb-3"}>
                          <label className={"text-dark h6"}>
                            Staff Email
                          </label>
                          <input
                            type="email"
                            onChange={(evt) =>
                              setAddingFormData({
                                ...addingFormData,
                                email: evt.target.value,
                              })
                            }
                            value={addingFormData.student_email}
                            className="form-control"
                            placeholder={"Student Email"}
                          />
                        </div>
                      </div>

                      {/* Address */}
                      <div className={"col-md-4"}>
                        <div className={"form-group mb-3"}>
                          <label className={"text-dark h6"}>Address</label>
                          <input
                            type="text"
                            onChange={(evt) =>
                              setAddingFormData({
                                ...addingFormData,
                                address: evt.target.value,
                              })
                            }
                            value={addingFormData.address}
                            className="form-control"
                            placeholder={"Full Address"}
                          />
                        </div>
                      </div>

                      {/* State */}
                      <div className={"col-md-4"}>
                        <div className={"form-group mb-3"}>
                          <label className={"text-dark h6"}>State</label>
                          <input
                            type="text"
                            onChange={(evt) =>
                              setAddingFormData({
                                ...addingFormData,
                                state: evt.target.value,
                              })
                            }
                            value={addingFormData.state}
                            className="form-control"
                            placeholder={"State"}
                          />
                        </div>
                      </div>

                      {/* City */}
                      <div className={"col-md-4"}>
                        <div className={"form-group mb-3"}>
                          <label className={"text-dark h6"}>City</label>
                          <input
                            type="text"
                            onChange={(evt) =>
                              setAddingFormData({
                                ...addingFormData,
                                city: evt.target.value,
                              })
                            }
                            value={addingFormData.city}
                            className="form-control"
                            placeholder={"City"}
                          />
                        </div>
                      </div>

                      {/* Profile Picture */}
                      <div className={"col-md-4"}>
                        <div className={"form-group mb-3"}>
                          <label className={"text-dark h6"}>
                            Profile Picture
                          </label>
                          <input
                            type="file"
                            onChange={(evt) =>
                              setAddingFormData({
                                ...addingFormData,
                                profile_picture: evt.target.value,
                              })
                            }
                            value={addingFormData.profile_picture}
                            className="form-control"
                            placeholder={"Profile Picture"}
                          />
                        </div>
                      </div>
                      
                      {/* Password */}
                      <div className={"col-md-4"}>
                        <div className={"form-group mb-3"}>
                          <label className={"text-dark h6"}>Password</label>
                          <input
                            type="password"
                            onChange={(evt) =>
                              setAddingFormData({
                                ...addingFormData,
                                password: evt.target.value,
                              })
                            }
                            value={addingFormData.password}
                            className="form-control"
                            placeholder={"*****"}
                          />
                        </div>
                      </div>
                    
                      {/* Joining Date */}
                      <div className={"col-md-4"}>
                        <div className={"form-group mb-3"}>
                          <label className={"text-dark h6"}>Joining Date</label>
                          <input
                            type="date"
                            onChange={(evt) =>
                              setAddingFormData({
                                ...addingFormData,
                                joining_date: evt.target.value,
                              })
                            }
                            value={addingFormData.joining_date}
                            className="form-control"
                          />
                        </div>
                      </div>

                       
                    </div>

                    {/* Buttons */}
                    <div className={"form-group px-3"}>
                      <button
                        className="btn btn-info rounded-0"
                        type={"submit"}
                      >
                        {isAddLaoded ? (
                          <div>
                            <i className="fas fa-plus"></i> Add
                          </div>
                        ) : (
                          <div>
                            <span
                              className="spinner-border spinner-border-sm mr-1"
                              role="status"
                              aria-hidden="true"
                            ></span>
                            Loading..
                          </div>
                        )}
                      </button>

                      <button
                        className="btn btn-secondary rounded-0 ml-2"
                        data-dismiss="modal"
                        id={"closeAddModalButton"}
                      >
                        Close
                      </button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>

          {/* -- Update Modal -- */}
          <div
            className="modal fade"
            id="updateModal"
            tabIndex="-1"
            role="dialog"
            aria-labelledby="updateModalLabel"
            aria-hidden="true"
          >
            <div className="modal-dialog modal-xl" role="document">
              <div className="modal-content rounded-0">
                <div className="modal-body">
                  <div className={"text-center"}>
                    <button type="button" className="close p-2 border" data-dismiss="modal" aria-label="Close">
                      <span aria-hidden="true">&times;</span>
                    </button>
                    <h4 className={"text-theme"}>Update Student Details</h4>
                    <div className={"devider m-auto"} />
                    
                  </div>

                  <form
                    onSubmit={updateSubmitHandler}
                    className="form-horizontal form-material"
                  >
                    {/* Personal Details */}
                    <div className={"row"}>
                      <div className={"col-md-12"}>
                        <h4 className={"text-theme"}>Personal Details</h4>
                        <div className={"devider"} />
                      </div>
                    
                    {/* Staff Full Name */}
                    <div className={"col-md-4"}>
                        <div className={"form-group mb-3"}>
                          <label className={"text-dark h6"}>
                            Staff Full Name
                          </label>
                          <input
                            type="text"
                            onChange={(evt) =>
                              setData({
                                ...data,
                                name: evt.target.value,
                              })
                            }
                            value={data.name}
                            className="form-control"
                            placeholder={"Eg. Rahul Kumar"}
                          />
                        </div>
                      </div>

                      {/* Gender */}
                      <div className={"col-md-4"}>
                        <div className={"form-group"}>
                          <label className={"text-dark h6"}>Gender</label>
                          <select
                            className="form-control"
                            onChange={(evt) =>
                              setData({
                                ...data,
                                gender: evt.target.value,
                              })
                            }
                          >
                            <option value={""} disabled selected hidden>
                              Select Gender
                            </option>
                            <option value={"MALE"} selected={data.gender=="MALE"?"selected":"none"}>MALE</option>
                            <option value={"FEMALE"} selected={data.gender=="FEMALE"?"selected":"none"}>FEMALE</option>
                            <option value={"OTHER"} selected={data.gender=="OTHER"?"selected":"none"}>OTHER</option>
                          </select>
                        </div>
                      </div>
 
                      {/* Staff Mobile */}
                      <div className={"col-md-4"}>
                        <div className={"form-group mb-3"}>
                          <label className={"text-dark h6"}>
                            Mobile
                          </label>
                          <input
                            type="tel"
                            onChange={(evt) =>
                              setData({
                                ...data,
                                mobile: evt.target.value,
                              })
                            }
                            value={data.mobile}
                            className="form-control"
                            placeholder={"Mobile NUmber"}
                          />
                        </div>
                      </div>

                      {/* Staff Email */}
                      <div className={"col-md-4"}>
                        <div className={"form-group mb-3"}>
                          <label className={"text-dark h6"}>
                            Staff Email
                          </label>
                          <input
                            type="email"
                            onChange={(evt) =>
                              setData({
                                ...data,
                                email: evt.target.value,
                              })
                            }
                            value={data.student_email}
                            className="form-control"
                            placeholder={"Student Email"}
                          />
                        </div>
                      </div>

                      {/* Address */}
                      <div className={"col-md-4"}>
                        <div className={"form-group mb-3"}>
                          <label className={"text-dark h6"}>Address</label>
                          <input
                            type="text"
                            onChange={(evt) =>
                              setData({
                                ...data,
                                address: evt.target.value,
                              })
                            }
                            value={data.address}
                            className="form-control"
                            placeholder={"Full Address"}
                          />
                        </div>
                      </div>

                      {/* State */}
                      <div className={"col-md-4"}>
                        <div className={"form-group mb-3"}>
                          <label className={"text-dark h6"}>State</label>
                          <input
                            type="text"
                            onChange={(evt) =>
                              setData({
                                ...data,
                                state: evt.target.value,
                              })
                            }
                            value={data.state}
                            className="form-control"
                            placeholder={"State"}
                          />
                        </div>
                      </div>

                      {/* City */}
                      <div className={"col-md-4"}>
                        <div className={"form-group mb-3"}>
                          <label className={"text-dark h6"}>City</label>
                          <input
                            type="text"
                            onChange={(evt) =>
                              setData({
                                ...data,
                                city: evt.target.value,
                              })
                            }
                            value={data.city}
                            className="form-control"
                            placeholder={"City"}
                          />
                        </div>
                      </div>
                      
                      {/* Status */}
                      <div className={"col-md-4"}>
                        <div className={"form-group"}>
                          <label className={"text-dark h6"}>Status</label>
                          <select
                            className="form-control"
                            onChange={(evt) =>
                              setData({
                                ...data,
                                status: evt.target.value,
                              })
                            }
                          >
                            <option value={""} disabled selected hidden>
                              Select Status
                            </option>
                            <option value={1} selected={data.status==1?"selected":"none"}>Active</option>
                            <option value={0} selected={data.status==0?"selected":"none"}>Disabled</option>
                          </select>
                        </div>
                      </div>
                      </div>
                      
                     
                    {/* Buttons */}
                    <div className={"form-group px-3"}>
                      <button
                        className="btn btn-info rounded-0"
                        type={"submit"}
                      >
                        {isUpdateLaoded ? (
                          <div>
                            <i className="fas fa-plus"></i> Update
                          </div>
                        ) : (
                          <div>
                            <span
                              className="spinner-border spinner-border-sm mr-1"
                              role="status"
                              aria-hidden="true"
                            ></span>
                            Loading..
                          </div>
                        )}
                      </button>

                      <button
                        className="btn btn-secondary rounded-0 ml-2"
                        data-dismiss="modal"
                        type={"button"}
                        id={"closeUpdateModalButton"}
                      >
                        Close
                      </button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
  
          {/* -- View Modal -- */}
          <div className="modal fade" id="viewModal">
            <div className="modal-dialog modal-lg" role="document">
              <div className="modal-content rounded-0">
                <div className="modal-body">
                  {/* Header Section */}
                  <div className={"text-center"}>
                    <button type="button" className="close p-2 border" data-dismiss="modal" aria-label="Close">
                      <span aria-hidden="true">&times;</span>
                    </button>
                    <h4 className={"text-theme"}>Staff Details</h4>
                    <div className={"devider m-auto"} />
                  </div>

                  {/* Profile Section */}
                  <div className={"row mt-2 px-2"}>
                    <div className={"col-3"}>
                      <img
                        src={
                          "https://complianz.io/wp-content/uploads/2019/03/placeholder-300x202.jpg"
                        }
                        className={"img img-fluid"}
                      />
                    </div>
                    <div className={"col-9 py-3"}>
                      <h5 className={"text-left"}>{viewData.name}</h5>
                      <h5 className={"text-left"}>{viewData.email}</h5>
                      <h5 className={"text-left"}>{viewData.mobile}</h5>
                    </div>
                  </div>

                  <div className={"row mt-2 px-2"}>
                      <div className={"col-md-12 table-responsive"}>
                        <table className={"table table-bordered"}>
                            <tbody>
                                <tr>
                                    <th className={"border p-1"}>Date of Joining</th>
                                    <td className={"p-1"}>{viewData.joining_date}</td>
                                    <th className={"border p-1"}>Status</th>
                                    <td className={"p-1"}>{viewData.status?<span className={"badge badge-info"}>Active</span>:<span className={"badge badge-danger"}>Disabled</span>}</td>
                                </tr>
                                <tr>
                                    <th className={"border p-1"}>Staff Mobile</th>
                                    <td className={"p-1"}>{viewData.mobile}</td>
                                    <th className={"border p-1"}>Staff Email</th>
                                    <td className={"p-1"}>{viewData.email || "Not Available"}</td>
                                </tr>
                                <tr>
                                    <th className={"border p-1"}>Gender</th>
                                    <td className={"p-1"}>{viewData.gender}</td>
                                    <th className={"border p-1"}>Address</th>
                                    <td className={"p-1"}>{viewData.address}</td>
                                </tr>
                                <tr>
                                    <th className={"border p-1"}>City</th>
                                    <td className={"p-1"}>{viewData.city}</td>
                                    <th className={"border p-1"}>State</th>
                                    <td className={"p-1"}>{viewData.state}</td>
                                </tr>
                                
                            </tbody>
                        </table>
                      </div>
                  </div>
                   
                  {/* Buttons */}
                  <div className={"form-group"}>
                    <button
                      className="btn btn-secondary rounded-0 ml-2"
                      data-dismiss="modal"
                      id={"closeViewModalButton"}
                      onClick={() => $("#closeMdalButton").click()}
                    >
                      Close
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>

          {/* -- Delete Modal -- */}
          <div
            className="modal fade"
            id="deleteModal"
            tabIndex="-1"
            role="dialog"
            aria-labelledby="updateModalLabel"
            aria-hidden="true"
          >
            <div className="modal-dialog" role="document">
              <div className="modal-content rounded-0">
                <div className="modal-body text-center">
                  <img
                    style={{ width: "150px" }}
                    className={"img img-fluid"}
                    src={
                      "https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcQ5R1g82DqzH4itsxpVCofNGWbAzKN_PJDBew&usqp=CAU"
                    }
                  />
                  <h4 className={"text-center mt-2"}>Do You Want to Delete?</h4>
                  
                  <div className={"form-group"}>
                    <button
                      className="btn btn-info rounded-0 px-3"
                      type={"submit"}
                      onClick={deleteSubmitHandler}
                    >
                      {isDeleteLaoded ? (
                        <div>
                          <i className="fas fa-trash"></i> Yes
                        </div>
                      ) : (
                        <div>
                          <span
                            className="spinner-border spinner-border-sm mr-1"
                            role="status"
                            aria-hidden="true"
                          ></span>
                          Loading..
                        </div>
                      )}
                    </button>
                    <button
                      className="btn btn-secondary rounded-0 ml-2 px-3"
                      data-dismiss="modal"
                      id={"closeDeleteModalButton"}
                    >
                      No
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default StaffRights;
